body {
  font-family: ver;
}
header {
  margin-left: 12%;
  width: 75%;
  padding: 20px 35px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  top: 0;
  border-bottom: 3px solid;
  
}

header h1 {
  font-weight: bold;
  font-size: 36px;
  margin: 0;
}
header nav ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}
header nav ul li {
  padding-top: 12px;
}
header nav ul li a {
  margin: 0 20px;
  font-weight: lighter;
  font-size: 1.00vw;
  text-decoration: none;
  color: black;
  font-family: ver;
}
.contact-me-button {
  background-color: #ffc727;
  border: none;
  padding: 2px;
  border-radius: 10px;
  width: 123px;
  height: 25px;
}
.contact-me-button a{
  color: white;
  font-weight: bold;
  margin-top: 10px;
}
.about {
  margin-bottom: 100px;
  color: black;
}
.about h2 {
  margin-left: 25%;
  margin-top: 5%;
  font-size: 68px;
}
.about p {
  margin-left: 28%;
  margin-top: 1%;
  font-size: 30px;
  width: 450px;
}
.image-icon {
  position: absolute;
  padding: 30px;
  
  width: 300px;
  height: 300px;
  margin-left: 60%;
  margin-top: -15%;
  background-image: url(../images/IMG_3810.PNG);
  background-size: cover;
  
}
.portfolio h2 {
  margin-left: 15%;
  margin-top: 5%;
  font-size: 48px;
}
.portfolio-works button{
  background-color: #ffc727;
  border: none;
  width: 350px;
  height: 500px;   
  margin-right: 11.5px;
  margin-left: 15px;
  border-radius: 10px;
}
.border-divison {
  display: inline;
  position: absolute;
  background-color: orange;
  border: 1px black solid;
  border-radius: 10px;
  margin-top: 110px;
  height: 300px;
}
.contact-me {
  margin-bottom: 100px;
}
.contact-me h2 {
  margin-left: 15%;
  margin-top: 5%;
  font-size: 48px;
}
.contact-info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-left: 30%;
  margin-top: 30px;
}
.contact-info a {
  padding: 10px 50px;
  font-size: 24px;
  border: none;
  margin-top: 20px;
  text-decoration: none;
  padding: 0px 30px;
  color: #ffc727;
  border-bottom: 3px solid black;
}

@media screen and (max-width: 980px) {
  .image-icon {
      margin-left: 70%;
      margin-top: -25%;
  }
  header nav ul li a {
      font-size: 2vw;
  }
  .portfolio {
      margin-left: 10%;
  }
  .portfolio-works button{
      margin-bottom: 10px;
  }
  .contact-info {
